.profile-page {
    margin-top: 2%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    text-align: center;
    padding: 20px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    min-height: 70vh;
}


.profile-info {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    background: #f0f2f5;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    width: 90%;
    max-width: 600px;
    justify-content: space-between;
}

.profile-img {
    width: 80px;
    height: 80px;
    margin-right: 20px;
    border-radius: 50%;
    overflow: hidden;
    background-color: #ddd;
    display: flex;
    align-items: center;
    justify-content: center;
}

.profile-details {
    flex: 1;
    text-align: left;
}

.profile-details p {
    margin: 5px 0;
    font-size: 1em;
}

.edit-icon {
    cursor: pointer;
    font-size: 1.2em;
    color: #888;
}

.edit-icon:hover {
    color: #333;
}

.profile-page h1 {
    font-size: 2em;
    margin-bottom: 20px;
    color: #333;
}

.profile-page h2 {
    font-size: 1.5em;
    color: #555;
    margin-bottom: 10px;
    text-align: left;
    width: 90%;
    max-width: 600px;
}

.saved-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.saved-posts, .saved-quizzes {
    width: 90%;
    max-width: 600px;
    margin-bottom: 20px;
    padding: 20px;
    background: #f0f2f5;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

.section-title {
    text-align: left;
    width: 100%;
    padding-left: 10px;
    font-size: 1.2em;
    font-weight: bold;
    margin-bottom: 10px;
}

.post-container, .quiz-container {
    display: flex;
    justify-content: space-between;
}

.post-container div, .quiz-container div {
    width: 22%;
    height: 100px;
    background-color: #e0e0e0;
    border-radius: 5px;
}

.logout-button {
    background-color: #f0f2f5;
    color: #333;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 1em;
    cursor: pointer;
    transition: background-color 0.3s;
    margin-top: 20px;
}

.logout-button:hover {
    background-color: #e0e0e0;
}

.delete-account {
    color: #ff4757;
    margin-top: 10px;
    cursor: pointer;
    font-size: 0.9em;
    text-decoration: underline;
}

.delete-account:hover {
    color: #e84118;
}


@media (max-width: 768px) {
    .profile-page {
        padding: 15px;
    }

    .profile-page h1 {
        font-size: 1.8em;
    }

    .profile-page h2 {
        font-size: 1.3em;
    }

    .profile-info {
        flex-direction: column;
        align-items: flex-start;
    }

    .profile-details {
        text-align: center;
    }
}


@media (max-width: 480px) {
    .profile-page {
        padding: 10px;
    }

    .profile-page h1 {
        font-size: 1.5em;
    }

    .profile-page h2 {
        font-size: 1.2em;
    }

    .profile-info {
        flex-direction: column;
        align-items: center;
    }

    .profile-img {
        width: 60px;
        height: 60px;
        margin-right: 0;
        margin-bottom: 10px;
    }

    .profile-details p {
        font-size: 0.9em;
    }

    .edit-icon {
        font-size: 1em;
    }

    .section-title {
        font-size: 1em;
    }

    .post-container div, .quiz-container div {
        width: 45%;
        height: 80px;
    }
}
